import React, { Component, useState } from 'react';

function App() {
    const [Myname, setMyName] = useState('Click me ðŸ‘†')
    const [bg1, setBg1] = useState('#8e44ad')
    const [bg2, setBg2] = useState('#9b59b6')
    const bgChange = () => {
        if (Myname == 'Click me ðŸ‘†') {
            setMyName('OuchðŸ˜µ');
            setBg1('Orange');
            setBg2('darkOrange');
        } else if (Myname == 'OuchðŸ˜µ') {
            setMyName('ManjaOye ðŸ˜ ');
            setBg1('red');
            setBg2('darkRed');
        } else {
            setMyName('Click me ðŸ‘†');
            setBg1('#8e44ad');
            setBg2('#9b59b6');
        }

    }

    return <>
        <div style={{ background: bg1 }}>
            <button style={{ background: bg2 }} onClick={bgChange} >
                {Myname}
            </button>
        </div>
    </>
}


export default App;